.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        //background: lightsalmon;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; // for firefox
        &::-webkit-scrollbar{
            display: none;
        }

        // every element(component in sections)
        > * {
            width: 100vw;
            height: calc(100vh - 70px);
            //scroll-snap-align:center;

        }
    }

    
}